@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
.input-group {
	width: 100%;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	position: relative;
}

.input-group--error {
	margin-bottom: 10px;
}

.input-group--error .input-group__input {
	border: 2px solid #ff7979;
}

.input-group__input {
	width: 100%;
	height: 56px;
	padding-left: 20px;
	background-color: #fff;
	border: 1px solid #dedede;
	border-radius: 5px;
	outline: none;
	font-weight: 600;
	font-size: 14px;
	letter-spacing: 0.25px;
	color: #3d3b48;
	caret-color: #5e54a4;
}

.input-group__input:focus:not(.input-group--error .input-group__input) {
	border: 1px solid #5e54a4;
}

.input-group__input::-webkit-input-placeholder {
	font-weight: 600;
	font-size: 14px;
	letter-spacing: 0.25px;
	color: #3d3b48;
	opacity: 0.75;
}

.input-group__input:-ms-input-placeholder {
	font-weight: 600;
	font-size: 14px;
	letter-spacing: 0.25px;
	color: #3d3b48;
	opacity: 0.75;
}

.input-group__input::placeholder {
	font-weight: 600;
	font-size: 14px;
	letter-spacing: 0.25px;
	color: #3d3b48;
	opacity: 0.75;
}

.input-group__icon {
	position: absolute;
	top: 50%;
	right: 10px;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.input-group__error {
	font-style: italic;
	font-weight: 500;
	font-size: 11px;
	text-align: right;
	color: #ff7979;
	position: absolute;
	right: 0;
}

.form {
	width: 100%;
	max-width: 540px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding: 24px;
	background-color: #fff;
	box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.15);
	border-radius: 10px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	grid-gap: 20px;
	gap: 20px;
	position: relative;
}

.form__legend {
	width: 100%;
	background-color: #5e54a4;
	box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.15);
	border-radius: 10px;
	font-size: 15px;
	font-weight: 400;
	line-height: 26px;
	text-align: center;
	letter-spacing: 0.267857px;
	color: #fff;
	padding: 20px 65px;
	position: absolute;
	top: -110px;
	left: 0;
}

.form__legend--bold {
	font-weight: 500;
}

.form__submit {
	width: 100%;
	height: 56px;
	background-color: #38cc8b;
	box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.09);
	border-radius: 5px;
	font-weight: 600;
	font-size: 15px;
	text-align: center;
	letter-spacing: 1px;
	color: #fff;
	border: none;
	transition: background 0.2s linear;
}

.form__submit:hover {
	cursor: pointer;
	background-color: #77e2b3;
}

.form__agreement {
	font-weight: 500;
	font-size: 11px;
	line-height: 21px;
	text-align: center;
	color: #bab7d4;
}

.form__agreement--highlighted {
	cursor: pointer;
	font-weight: 700;
	color: #ff7979;
}

@media (min-width: 992px) {
	.form {
		padding: 40px;
	}

	.form__legend {
		top: -90px;
	}
}

.container {
	width: 100%;
	max-width: 1160px;
	height: 95vh;
	padding: 0 10px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.container__content {
	text-align: center;
}

.container__title {
	font-weight: 700;
	font-size: 28px;
	line-height: 36px;
	text-align: center;
	letter-spacing: -0.291667px;
	color: #fff;
	margin-bottom: 15px;
}

.container__text {
	font-weight: 500;
	font-size: 16px;
	line-height: 26px;
	text-align: center;
	color: #fff;
}

.container__message {
	width: 100%;
	max-width: 540px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	margin: auto 0;
	padding: 24px;
	background-color: #fff;
	box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.15);
	border-radius: 10px;
	color: #38cc8b;
	text-align: center;
}

@media (min-width: 992px) {
	.container {
		padding: 0 30px;
		-webkit-flex-direction: row;
		        flex-direction: row;
		grid-gap: 0;
		gap: 0;
	}

	.container__title {
		font-size: 50px;
		line-height: 55px;
		text-align: start;
	}

	.container__text {
		line-height: 26px;
		text-align: start;
	}
}

/* FORM ANIMATION - EXIT */
.container__form--exit {
	-webkit-transform: scale(1);
	        transform: scale(1);
	opacity: 1;
}

.container__form--exit-active {
	-webkit-transform: scale(0.1);
	        transform: scale(0.1);
	opacity: 0;
	transition: all 250ms linear;
}

/* MESSAGE ANIMATION - ENTER */
.container__message--enter {
	-webkit-transform: scale(0.1);
	        transform: scale(0.1);
	opacity: 0;
}

.container__message--enter-active {
	-webkit-transform: scale(1);
	        transform: scale(1);
	opacity: 1;
	transition: all 250ms linear;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Poppins", sans-serif;
}

body {
	width: 100vw;
	height: 100vh;
	background-color: #ff7979;
	background-image: url(/form-validation/static/media/bg-intro-mobile.02ce3982.png);
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

@media (min-width: 992px) {
	body {
		background-image: url(/form-validation/static/media/bg-intro-desktop.059183ee.png);
	}
}

